@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
}

html,
body {
  font-size: 10px;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

@font-face {
  font-family: "Gilroy";
  font-style: bold;
  font-weight: 700;
  src: url(../fonts/Gilroy-Bold.ttf) format("truetype");
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/Gilroy-Regular.ttf) format("truetype");
}
@font-face {
  font-family: "Gilroy";
  font-style: medium;
  font-weight: 500;
  src: url(../fonts/Gilroy-Medium.ttf) format("truetype");
}
@font-face {
  font-family: "Gilroy";
  font-style: light;
  font-weight: 300;
  src: url(../fonts/Gilroy-Light.ttf) format("truetype");
}
@font-face {
  font-family: "Gilroy";
  font-style: semibold;
  font-weight: 600;
  src: url(../fonts/Gilroy-SemiBold.ttf) format("truetype");
}
@font-face {
  font-family: "Gilroy";
  font-style: extrabold;
  font-weight: 800;
  src: url(../fonts/Gilroy-ExtraBold.ttf) format("truetype");
}
* {
  font-family: Gilroy;
  color: #252525;
}

body {
  background-color: #F5F3EA;
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0 2rem;
}

@media screen and (min-width: 768px) {
  .container {
    padding: 0 3rem;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    padding: 0 7rem;
  }
}
.container--header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.header__menu {
  font-weight: 600;
  font-size: 2.5rem;
}
.header__logo-text {
  display: none;
}

.container--page-title {
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-title__title {
  font-size: 5.7rem;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
}
.page-title__subtitle {
  font-size: 5.7rem;
  font-weight: 300;
  text-transform: capitalize;
  margin-bottom: 3rem;
}
.page-title__link {
  margin-bottom: 2rem;
}

@media screen and (min-width: 768px) {
  .header__logo {
    display: flex;
    align-items: center;
  }
  .header__logo-text {
    display: inline;
    font-size: 3.5rem;
    line-height: normal;
    letter-spacing: -0.7px;
    text-transform: uppercase;
    margin-left: 1rem;
  }
  .header__logo-text strong {
    font-weight: 800;
    text-transform: none;
  }
  .page-title__title {
    font-size: 12rem;
  }
  .page-title__subtitle {
    font-size: 12rem;
  }
  .page-title__link {
    height: 4rem;
    margin-bottom: 4rem;
  }
}
.about-me__text {
  text-align: center;
  font-size: 3rem;
  font-weight: 300;
  line-height: normal;
  padding-top: 3rem;
  margin-bottom: 3rem;
}
.about-me__text strong {
  font-weight: 800;
}
.about-me__wrapper {
  background-color: #252525;
  padding-top: 4rem;
  padding-bottom: 4rem;
  margin: 0 -2rem;
}
.about-me__title {
  color: #fff;
  text-align: right;
  font-size: 2.8rem;
  font-weight: 400;
  line-height: 2.8rem;
  text-transform: uppercase;
  margin-bottom: 3rem;
  padding: 0 2rem;
}
.about-me__links {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
  padding: 0 2rem;
}

.container--logos {
  padding-top: 4rem;
  padding-bottom: 4rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.logos__item {
  height: 7rem;
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 4rem;
  background: #fff;
  border-radius: 3.5rem;
}

@media screen and (min-width: 768px) {
  .about-me__text {
    font-size: 5.4rem;
  }
}
@media screen and (min-width: 1200px) {
  .container--about-me {
    flex-direction: row;
    padding-top: 7rem;
    padding-bottom: 7rem;
  }
  .about-me__wrapper {
    margin-right: -7rem;
    padding: 6rem 7rem 6rem 11.5rem;
    margin-left: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .about-me__text {
    font-size: 6.2rem;
    margin: 0 7rem 0 0;
    align-self: stretch;
    padding: 1rem;
    text-align: left;
  }
  .logos {
    border-top: 1px solid #252525;
    padding: 3rem 0;
  }
}
.do {
  border-bottom: 1px solid #252525;
  border-top: 1px solid #252525;
  padding-top: 100vw;
  background: url(../images/do/Image.jpg) no-repeat;
  background-position: top;
  background-size: contain;
}
.do__title {
  text-align: center;
  font-size: 8rem;
  font-weight: 300;
  line-height: 8rem;
  text-transform: uppercase;
  margin-bottom: 4rem;
  padding-top: 4rem;
}
.do__list-title {
  font-size: 3.5rem;
  font-weight: 600;
  line-height: normal;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.do__list-title::before {
  content: attr(number);
  font-weight: 400;
  margin-right: 1rem;
}
.do__list-desc {
  font-family: Inter;
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.8rem;
  margin-bottom: 3rem;
  padding-bottom: 3rem;
  border-bottom: 1px solid #252525;
}
.do__list-desc:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

@media screen and (min-width: 768px) {
  .do__title {
    font-size: 15rem;
    margin-bottom: 4rem;
  }
  .do__list-title {
    font-size: 4.4rem;
  }
}
@media screen and (min-width: 1200px) {
  .do {
    padding: 0 0 0 50vw;
    background-position: left;
  }
  .do__title {
    padding-top: 7rem;
    margin-bottom: 10rem;
    text-align: start;
  }
}
.selected-work {
  padding-top: 3rem;
  border-bottom: 1px solid #252525;
  padding-bottom: 3rem;
}
.selected-work__wrapper {
  display: grid;
  gap: 2rem;
}
.selected-work__title {
  text-align: center;
  font-size: 8rem;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.selected-work__box-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 3rem;
  margin-bottom: 2rem;
}
.selected-work__box-wrapper--right {
  justify-content: flex-end;
  margin-bottom: 2rem;
}
.selected-work__box-text {
  text-align: left;
  font-size: 3.5rem;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  padding-top: 3rem;
  margin-bottom: 3rem;
}
.selected-work__box-btn {
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  height: 7rem;
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 3rem;
  background: #fff;
  border-radius: 3.5rem;
  gap: 1rem;
}
.selected-work__box-btn--dark {
  background: #252525;
  color: #FFF;
  text-align: right;
}
.selected-work__last-btn {
  display: flex;
  justify-content: center;
}
.selected-work__last-btn a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 7rem;
  flex: 0 0 100%;
  padding: 1.5rem 6rem;
  border-radius: 5rem;
  background: #fff;
  font-size: 1.8rem;
  font-weight: 400;
}

@media screen and (min-width: 768px) {
  .selected-work__last-btn a {
    flex: 0 0 auto;
  }
}
@media screen and (min-width: 1200px) {
  .selected-work__title {
    padding-top: 5rem;
    margin-bottom: 5rem;
    font-size: 21rem;
    letter-spacing: 8.48px;
  }
  .selected-work__wrapper {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 4rem;
  }
  .selected-work__box {
    order: 200;
    position: relative;
  }
  .selected-work__box:nth-child(3) {
    order: 100;
  }
  .selected-work__box-wrapper--right {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-bottom: 0;
  }
  .selected-work__last-btn {
    padding-top: 5rem;
    margin-bottom: 10rem;
  }
}
.clients {
  padding-bottom: 3rem;
  padding-top: 3rem;
  border-bottom: 1px solid #252525;
}
.clients__item {
  margin-bottom: 3rem;
  padding-bottom: 3rem;
  border-bottom: 1px solid #252525;
}
.clients__item:last-child {
  border-bottom: none;
  margin-bottom: 0;
}
.clients__title {
  font-size: 8rem;
  font-weight: 300;
  line-height: normal;
  text-transform: uppercase;
  margin-bottom: 3rem;
  text-align: center;
}
.clients__title--logo {
  margin-bottom: 3rem;
  padding-top: 4rem;
}
.clients__title--text h2 {
  padding-top: 3rem;
  font-size: 4rem;
  font-weight: 800;
  line-height: 4.4rem;
}
.clients__title--text p {
  padding-top: 3rem;
  font-family: Inter;
  font-size: 2rem;
  font-weight: 400;
  line-height: 3rem;
}
.clients__title--footer {
  display: grid;
  grid-template-columns: 10rem 25rem;
  grid-template-rows: 4rem 4rem;
  grid-template-areas: "foto text" "foto prof";
  padding-top: 4rem;
}
.clients__title--footer-foto {
  grid-area: foto;
  width: 7rem;
  height: 7rem;
}
.clients__title--footer-text {
  grid-area: text;
  font-family: Inter;
  font-size: 2rem;
  font-weight: 600;
  line-height: 2rem;
}
.clients__title--footer-prof {
  grid-area: prof;
  font-family: Inter;
  font-size: 2rem;
  font-weight: 400;
  line-height: 2rem;
}

.container-clients {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 768px) {
  .clients__title {
    font-size: 15rem;
  }
}
@media screen and (min-width: 1200px) {
  .clients {
    padding-top: 5rem;
  }
  .clients__title {
    font-size: 21rem;
    margin-bottom: 5rem;
  }
  .clients__item {
    flex: 1 0 33.333%;
    padding: 3rem 5rem 6rem 5rem;
    margin-bottom: 0;
    border-right: 1px solid #252525;
  }
  .clients__item:nth-child(3n) {
    border-right: none;
  }
  .clients__item:nth-child(n+4) {
    border-bottom: none;
  }
  .container-clients {
    flex-wrap: wrap;
    flex-direction: row;
    margin-bottom: 8rem;
  }
}
.our-team {
  padding-top: 3rem;
}
.our-team__item {
  margin-bottom: 4rem;
}
.our-team__title {
  margin-bottom: 5rem;
  font-size: 8rem;
  font-weight: 300;
  line-height: normal;
  text-transform: uppercase;
  text-align: center;
}
.our-team__name {
  margin-bottom: 0.5rem;
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
}
.our-team__name-prof {
  text-align: center;
  font-size: 2rem;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 1.5rem;
}
.our-team__social-media {
  padding-top: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

@media screen and (min-width: 768px) {
  .our-team__wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .our-team__item {
    width: calc(50% - 2rem);
  }
  .our-team__title {
    font-size: 15rem;
  }
  .our-team__name {
    text-align: left;
  }
  .our-team__name-prof {
    text-align: left;
  }
  .our-team__social-media {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
  }
}
@media screen and (min-width: 1200px) {
  .our-team__title {
    font-size: 21rem;
    letter-spacing: 8.48px;
  }
  .our-team__wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 4rem;
  }
  .our-team__item {
    width: auto;
    flex: 1 0 calc(24% - 2rem);
  }
}
.join__title {
  text-align: center;
  font-size: 3rem;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  margin-bottom: 2rem;
  padding-top: 6rem;
}
.join__title-text {
  text-align: center;
  font-size: 4rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
}
.join__btn-wrapper {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 5rem;
}
.join__btn {
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  height: 7rem;
  flex: 1 0 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 3rem;
  border-radius: 3.5rem;
  gap: 1rem;
  background: #252525;
  color: #FFF;
  text-align: right;
}

.contact {
  padding-bottom: 5rem;
  border-top: 1px solid #252525;
}
.contact__title {
  text-align: center;
  font-size: 8rem;
  font-weight: 300;
  line-height: normal;
  text-transform: uppercase;
  padding-top: 3rem;
  margin-bottom: 3rem;
}
.contact__title--name {
  text-align: center;
  font-size: 5rem;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  margin-bottom: 3rem;
}
.contact__title--text {
  text-align: center;
  font-size: 3rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 3rem;
}
.contact__btn-wrapper {
  display: flex;
  justify-content: center;
}
.contact__btn {
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  height: 7rem;
  flex: 1 0 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem 3rem;
  border-radius: 3.5rem;
  gap: 1rem;
  background: #252525;
  color: #FFF;
  text-align: right;
}

@media screen and (min-width: 768px) {
  .join__title {
    text-align: left;
  }
  .join__title-text {
    font-size: 6.4rem;
    text-align: left;
  }
  .join__btn {
    justify-content: flex-start;
    flex: 0 0 auto;
    margin-bottom: 5rem;
  }
  .contact {
    border-top: 1px solid #252525;
  }
  .contact__title {
    font-size: 15rem;
  }
  .contact__title--name {
    font-size: 6.4rem;
  }
  .contact__title--text {
    font-size: 3.4rem;
  }
  .contact__btn {
    flex: 0 0 auto;
  }
}
@media screen and (min-width: 1200px) {
  .join__wrapper {
    flex: 1 1 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 15px;
  }
  .container__contact-wrapper {
    display: flex;
    flex-direction: row;
    padding-left: 0;
    padding-right: 0;
    padding-top: 10rem;
    margin-bottom: 5rem;
    gap: 4rem;
  }
  .contact__title-wrapper {
    width: 50%;
  }
  .contact__title-wrapper h2 {
    padding-top: 0;
    text-align: left;
  }
  .contact__wrapper {
    width: 50%;
  }
  .contact__title--name {
    text-align: left;
  }
  .contact__title--text {
    text-align: left;
  }
  .contact__btn-wrapper {
    justify-content: left;
  }
}
.footer {
  border-top: 1px solid #252525;
}
.footer__title {
  padding-top: 2rem;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: normal;
  text-align: center;
}
.footer__social-media {
  gap: 1rem;
  display: flex;
  justify-content: center;
  padding-top: 2rem;
  margin-bottom: 2rem;
}

@media screen and (min-width: 768px) {
  .footer {
    padding: 2rem 0 2rem 0;
  }
  .container-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
  }
}